@font-face {
  font-family: "Bitter";
  font-weight: 300;
  font-style: normal;
  src: url("../fonts/bitter/Bitter-Light.woff2") format("woff2"), url("../fonts/bitter/Bitter-Light.woff") format("woff");
}
@font-face {
  font-family: "Bitter";
  font-weight: 400;
  font-style: normal;
  src: url("../fonts/bitter/Bitter-Regular.woff2") format("woff2"), url("../fonts/bitter/Bitter-Regular.woff") format("woff");
}
@font-face {
  font-family: "Bitter";
  font-weight: 500;
  font-style: normal;
  src: url("../fonts/bitter/Bitter-Medium.woff2") format("woff2"), url("../fonts/bitter/Bitter-Medium.woff") format("woff");
}
@font-face {
  font-family: "Bitter";
  font-weight: 600;
  font-style: normal;
  src: url("../fonts/bitter/Bitter-SemiBold.woff2") format("woff2"), url("../fonts/bitter/Bitter-SemiBold.woff") format("woff");
}
@font-face {
  font-family: "Bitter";
  font-weight: 700;
  font-style: normal;
  src: url("../fonts/bitter/Bitter-Bold.woff2") format("woff2"), url("../fonts/bitter/Bitter-Bold.woff") format("woff");
}
.container {
  max-width: 1375px;
  margin: 0 auto;
  padding: 0 15px;
}

@media (min-width: 576px) {
  .container {
    max-width: 540px;
  }
}
@media (min-width: 769px) {
  .container {
    max-width: 720px;
  }
}
@media (min-width: 992px) {
  .container {
    max-width: 960px;
  }
}
@media (min-width: 1200px) {
  .container {
    max-width: 1375px;
  }
}
html {
  box-sizing: border-box;
  min-height: 100%;
}

*, *:before, *:after {
  margin: 0;
  padding: 0;
  box-sizing: inherit;
}

body {
  min-height: 100vh;
  font-family: "Bitter", sans-serif;
  position: relative;
  font-weight: 400;
  font-size: 17px;
  line-height: 20px;
}

a[href^="tel:"] {
  white-space: nowrap;
}

input, button, textarea {
  font-family: "Bitter", sans-serif;
  outline: none;
  -webkit-appearance: none;
}

button {
  background: none;
}

textarea {
  resize: none;
}

input[type=submit] {
  cursor: pointer;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

ul {
  margin: 14px 0;
  padding: 0;
  list-style: none;
}

img {
  max-width: 100%;
  max-height: 100%;
}

p {
  margin: 12px 0;
}

h1 {
  margin-bottom: 24px;
  font-weight: 700;
  font-size: 36px;
  line-height: 38px;
  letter-spacing: -1px;
  font-family: "Bitter", sans-serif;
}

h2 {
  margin: 0 0 12px;
  font-weight: 700;
  font-size: 30px;
  line-height: 35px;
  font-family: "Bitter", sans-serif;
}

h3 {
  margin: 0 0 8px;
  font-weight: 700;
  font-size: 24px;
  line-height: 28px;
  font-family: "Bitter", sans-serif;
}

a {
  text-decoration: none;
  transition: all 0.3s ease-in-out;
}
a:hover {
  text-decoration: none;
}

body.overflow {
  height: 100vh;
  overflow: hidden;
}

body {
  display: flex;
  flex-direction: column;
  transition: transform 0.3s ease;
}
body.is-open-menu {
  transform: translateX(270px);
}

html {
  overflow-x: hidden;
}

.main-content {
  flex: auto;
}

.subtitle {
  margin-bottom: 24px;
  font-size: 24px;
  line-height: 28px;
}

.section {
  padding: 30px 0 40px;
}
.section--min {
  padding: 10px 0 40px;
}

.text-center {
  text-align: center;
}

.text-left {
  text-align: left;
}

.text-right {
  text-align: right;
}

.fix {
  position: fixed !important;
  top: 0;
  left: 0;
  right: 0;
  z-index: 1000;
  animation: fadeInUp 0.5s ease;
}

#top {
  width: 40px;
  height: 40px;
  cursor: pointer;
  display: none;
  position: fixed;
  right: 60px;
  bottom: 60px;
  z-index: 50;
}
#top svg {
  max-width: 100%;
  max-height: 100%;
}

.m-0 {
  margin: 0;
}

.test {
  outline: 1px solid red;
}

.map-wrap {
  position: relative;
  height: 100%;
}
.map-wrap iframe {
  border: none;
  height: 100%;
  min-height: 500px;
  width: 100%;
}

.color-white {
  color: #fff;
}

.columns-2 {
  columns: 2;
  column-gap: 20px;
}
.columns-2 p {
  margin-top: 0;
}

.video-wrap {
  position: relative;
  padding-bottom: 56.25%;
  width: 100%;
  height: 0;
  overflow: hidden;
}
.video-wrap iframe {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  border-width: 0;
  outline-width: 0;
}

@keyframes fadeInUp {
  0% {
    display: none;
    opacity: 0;
    transform: translateY(-100%);
  }
  1% {
    display: block;
    opacity: 0;
    transform: translateY(-100%);
  }
  100% {
    display: block;
    transform: translateY(0);
    opacity: 1;
  }
}
@keyframes rotate {
  0% {
    transform: rotate(0);
  }
  100% {
    transform: rotate(360deg);
  }
}
.langs-toggle {
  position: absolute;
  top: 0;
  left: 0;
  display: flex;
  flex-wrap: wrap;
}
.langs-toggle__link {
  position: relative;
  display: block;
  padding: 8px 10px;
  min-width: 52px;
  font-size: 14px;
  line-height: 16px;
  letter-spacing: -0.02em;
  color: #fff;
  background: #746B60;
  text-align: center;
}
.langs-toggle__link.is-active {
  background: #fff;
  color: #000;
}
.langs-toggle__link.is-active::after {
  content: "";
  position: absolute;
  top: 100%;
  left: 0;
  border-top: 5px solid #fff;
  border-right: 5px solid transparent;
  border-bottom: 5px solid transparent;
  border-left: 5px solid #fff;
}

@media (max-width: 991px) {
  .langs-toggle {
    left: auto;
    right: 0;
  }
  .langs-toggle__link {
    background: #9F9889;
  }
}
.social-list {
  display: flex;
  align-items: center;
}
.social-list__link {
  display: block;
  height: 26px;
  max-width: 26px;
  margin-right: 45px;
}
.social-list__link:hover {
  opacity: 0.6;
}
.social-list__link:last-child {
  margin-right: 0;
}
.social-list__link svg {
  max-width: 100%;
  max-height: 100%;
}

@media (max-width: 991px) {
  .social-list {
    flex-direction: column;
    height: 100%;
    justify-content: space-between;
  }
  .social-list__link {
    margin-right: 0;
    margin-bottom: 10px;
  }
  .social-list__link:last-child {
    margin-bottom: 0;
  }
}
.header {
  background: #EAE3D3;
}

.header-top {
  position: relative;
}

.header-top-menu {
  display: none;
}
.header-top-menu__link {
  display: block;
  width: 20px;
  height: 20px;
  margin-right: 28px;
  border: none;
}
.header-top-menu__link:last-child {
  margin-right: 0;
}
.header-top-menu__link svg {
  width: 100%;
  height: 100%;
  stroke: #A95508;
  stroke-width: 2px;
}

.header-main {
  display: grid;
  grid-template-columns: 392px auto 1fr auto;
  grid-template-areas: "a b c d" "a e e e";
  padding: 97px 0;
  align-items: end;
}
.header-main__logo {
  display: flex;
  align-items: end;
  grid-area: a;
}
.header-main__social {
  display: flex;
  height: 26px;
  align-items: center;
  grid-area: b;
}
.header-main__line {
  display: flex;
  height: 26px;
  align-items: center;
  grid-area: c;
}
.header-main__eye {
  display: flex;
  height: 26px;
  align-items: center;
  grid-area: d;
}
.header-main__nav {
  display: flex;
  align-items: center;
  margin-top: 94px;
  grid-area: e;
}

.logo {
  display: inline-block;
}
.logo__img {
  display: block;
}

.eye-toggle {
  display: inline-block;
  position: relative;
  padding-left: 44px;
  font-weight: 600;
  font-size: 16px;
  line-height: 21px;
  color: #2A2926;
  text-transform: uppercase;
}
.eye-toggle:hover {
  color: #C56C1A;
}
.eye-toggle:hover .eye-toggle__icon {
  stroke: #C56C1A;
}
.eye-toggle__icon {
  position: absolute;
  top: 0;
  left: 0;
  width: 28px;
  height: 24px;
  stroke: #2A2926;
  stroke-width: 2px;
  transition: stroke 0.3s ease;
}

.header-line-dotted {
  flex: auto;
  margin: 0 40px;
  height: 1px;
  background-image: linear-gradient(90deg, #C56C1A, #C56C1A 1px, transparent 1px, transparent 3px, transparent 100%);
  background-size: 5px 1px;
}

.main-menu-nav {
  flex: 1;
}

.main-menu {
  display: flex;
  justify-content: space-between;
  margin: 0;
}
.main-menu__link {
  position: relative;
  font-weight: 400;
  font-size: 16px;
  line-height: 18px;
  color: #2A2926;
  text-transform: uppercase;
}
.main-menu__link::after {
  content: "";
  width: 100%;
  height: 1px;
  background: #C56C1A;
  position: absolute;
  bottom: 0;
  left: 0;
  opacity: 0;
  transition: opacity 0.3s ease;
}
.main-menu__link:hover, .main-menu__link.is-active {
  color: #C56C1A;
}
.main-menu__link:hover::after, .main-menu__link.is-active::after {
  opacity: 1;
}

.header-search {
  margin-left: 82px;
}
.header-search__btn {
  display: block;
  border: none;
  width: 20px;
  height: 20px;
  cursor: pointer;
}
.header-search__btn:hover svg {
  stroke: #C56C1A;
}
.header-search__btn svg {
  width: 100%;
  height: 100%;
  stroke: #2A2926;
  stroke-width: 2px;
  transition: stroke 0.3s ease;
}

@media (max-width: 1200px) {
  .header-main {
    grid-template-columns: 300px auto 1fr auto;
  }
  .header-search {
    margin-left: 20px;
  }
  .main-menu__link {
    font-size: 14px;
  }
}
@media (max-width: 991px) {
  .header-top {
    position: static;
    padding: 33px 0 10px;
  }
  .header-top-menu {
    display: flex;
  }
  .header-search {
    display: none;
  }
  .header-line-dotted {
    display: none;
  }
  .header-main {
    padding: 46px 0;
    grid-template-columns: 1fr 30px;
    grid-template-areas: "a b" "c b" "d b";
    align-items: start;
    column-gap: 40px;
  }
  .header-main__social {
    height: 100%;
    grid-area: b;
  }
  .header-main__eye {
    height: auto;
    margin-top: 50px;
    grid-area: c;
  }
  .header-main__line {
    display: none;
  }
  .header-main__nav {
    margin-top: 0;
    display: block;
    position: fixed;
    top: 0;
    left: -270px;
    z-index: 1000;
    transition: transform 0.3s ease;
  }
  .header-main__nav.is-active::after {
    content: "";
    position: absolute;
    top: 37px;
    right: -10px;
    border-top: 6px solid transparent;
    border-right: none;
    border-bottom: 6px solid transparent;
    border-left: 10px solid #F6F3EA;
  }
  .main-menu-nav {
    padding: 65px 43px;
    width: 270px;
    height: 100vh;
    overflow-y: auto;
    background: #F6F3EA;
  }
  .main-menu {
    display: block;
  }
  .main-menu__item {
    margin-bottom: 30px;
  }
  .main-menu__link {
    display: block;
    font-size: 16px;
    line-height: 24px;
    text-align: right;
  }
  .eye-toggle {
    padding-left: 60px;
    line-height: 24px;
    color: #746B60;
  }
  .eye-toggle__icon {
    top: 50%;
    width: 43px;
    height: 26px;
    stroke: #746B60;
    transform: translateY(-50%);
  }
}